.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.center {
  text-align: center;
}

.putButtonInMiddle {
  margin: auto;
  width: 350px;
  padding: 10px;
}

.putBlockInMiddle {
  margin: auto;
  width: 51%;
  padding: 10px;
}

.leftHalfDiv {
  left: 50%;
  padding-left: 5%;
  padding-right: 5%;
  margin-right: 50%;
}

.rightHalfDiv {
  right: 50%;
  padding-left: 5%;
  padding-right: 5%;
  margin-left: 50%;
}