.logo {
    padding-right: 10px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
  }

  li {
    float: left;
  }

  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }

  li a:hover {
    background-color: #111;
  }

  .siteLogo {
      height: 20px;
      margin-left: 10px;
      margin-right: 10px;
  }

  .active {
    background-color: #0d87bd;
  }

  .headerPosition {
    z-index: 1;
  }