body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.center {
  text-align: center;
}

.putButtonInMiddle {
  margin: auto;
  width: 350px;
  padding: 10px;
}

.putBlockInMiddle {
  margin: auto;
  width: 51%;
  padding: 10px;
}

.leftHalfDiv {
  left: 50%;
  padding-left: 5%;
  padding-right: 5%;
  margin-right: 50%;
}

.rightHalfDiv {
  right: 50%;
  padding-left: 5%;
  padding-right: 5%;
  margin-left: 50%;
}
.input-group {
  width: 40% !important;
  margin: auto;
  padding: 10px;
}

.middle-column {
  margin-left: 25%;
  margin-right: 25%;
  text-align: center;
}

.input-width {
  width: 70%;
}

.center-test {
  position: fixed;
  /* center the element */
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  /* give it dimensions */
  min-height: 10em;
  width: 90%;
}

.title {
  text-align: center;
  font-size: 30px;
}
.logo {
    padding-right: 10px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
  }

  li {
    float: left;
  }

  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }

  li a:hover {
    background-color: #111;
  }

  .siteLogo {
      height: 20px;
      margin-left: 10px;
      margin-right: 10px;
  }

  .active {
    background-color: #0d87bd;
  }

  .headerPosition {
    z-index: 1;
  }
.logo {
    height: 100px;
    margin: auto;
    padding: 10px;
}
.spacing {
    margin-top: 20px;
    margin-bottom: 20px;
}

.buttonWrapper {
    margin-top: 20px;
    margin-bottom: 20px;
    height: 50px;
    /* width: 350px; */
    background-color: #333333;
    text-align: center;
    border-radius: 5px;
    border-width: 4px;
    border-style: solid;
    border-color: #d50737;
}

.buttonText {
    color: whitesmoke;
    vertical-align: middle;
}

/* className="center" */
.wrapper {
    height: auto;
    background-color: #cccccc;
    border-style: solid;
    border-width: 2px;
    display:inline-block;
}

.centerText {
    text-align: center;
    padding: 10px;
}

.centerDiv {
    margin: auto;
    width: 50%;
    padding: 10px;
}

.image {
    width: 40%;
    float: right;
    margin: 4px;
}
.footer {
    height: 90px;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #333333;
    color: white;
    text-align: center;
}

#title, #email, #address {
    margin: 0px;
    padding: 1px;
}

#title {
    font-size: 5px;
}
